* {
	box-sizing: border-box;
}
html {
	//height: 100%;
}
body {
	min-height: 100vh;
	margin: 0;
	padding: 0 20px;
	scroll-behavior: smooth;
	text-rendering: optimizeSpeed;
	font-family: Calibri, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background-color: #86a3d9;
	background-image: linear-gradient(to bottom, rgba(134, 163, 217, 0.7), transparent),
						url(bg.png),
						linear-gradient(to bottom, transparent, #86a3d9, #2a4b86);
	background-attachment: fixed;
	color: #fff;
}
button, input, select {
	font-family: inherit;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}